import { useState, useCallback, useRef, useEffect, Fragment } from 'react';
import { getAuth, signInWithPhoneNumber, RecaptchaVerifier } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import OtpInput from 'react-otp-input';

export default function Login() {
	const auth = useRef();
	const navigate = useNavigate();
	const [phoneCodes, setPhoneCodes] = useState([])
	const [error, setError] = useState(null)

	useEffect(() => {
		auth.current = getAuth();
		auth.current.useDeviceLanguage();
		window.recaptchaVerifier = new RecaptchaVerifier('recaptcha-container', {
			'size': 'invisible',
		}, auth.current);
			fetch('/phones.json')
				.then((r) => r.json())
				.then(o => setPhoneCodes(o))
	}, []);

	const [phone, setPhone] = useState('')
	const [countryCode, setCountryCode] = useState('+91')
	const [sendOTPLoading, setSendOTPLoading] = useState(false)

	const [otp, setOTP] = useState('')
	const [showOtp, setShowOTP] = useState(false)
	const [verifyOTPLoading, setVerifyOTPLoading] = useState(false)

	const sendOTP = useCallback((e) => {
		setSendOTPLoading(true);
		e.preventDefault();
		const phoneNumber = `${countryCode}${phone}`
		signInWithPhoneNumber(auth.current, phoneNumber, window.recaptchaVerifier)
				.then((confirmationResult) => {
					// SMS sent. Prompt user to type the code from the message, then sign the
					// user in with confirmationResult.confirm(code).
					window.confirmationResult = confirmationResult;
					setShowOTP(true);
					setSendOTPLoading(false);
					setError(null)
					// ...
				}).catch((error) => {
					// Error; SMS not sent
					if (error.code === 'auth/invalid-phone-number') {
						setError('Please enter a valid phone number');
					} else if (error.code === 'auth/too-many-requests') {
						setError('Too many requests. Please try again later');
					} else {
						setError('Something went wrong. Please try again later');
					}
					setSendOTPLoading(false);
				});
	}, [phone, countryCode])

	const verifyOTP = useCallback((e) => {
		setVerifyOTPLoading(true);
		e.preventDefault();
		window.confirmationResult.confirm(otp).then(() => {
			// success
			navigate('/')
			setVerifyOTPLoading(false)
			setError(null)
		}).catch((error) => {
			if (error.code === 'auth/invalid-verification-code') {
				setError('Invalid OTP');
			} else if (error.code === 'auth/too-many-requests') {
				setError('Too many requests. Please try again later');
			} else {
				setError('Something went wrong. Please try again later');
			}
			setVerifyOTPLoading(false)
		})
	}, [otp, navigate]);

	return (
		<Fragment>
			<p>Login with your phone number</p>
			{error && <p className='error'>{ error }</p>}
			<form onSubmit={(e) => sendOTP(e)}>
				<div style={{display: 'flex'}}>
					<div className='select'>
						<select disabled={showOtp} value={countryCode} onChange={e=>setCountryCode(e.target.key)}>
							{phoneCodes.map((o) => <option key={o.code} value={o.dial_code}>{`${o.emoji} ${o.dial_code}` }</option>)}
						</select>
					</div>
					<input disabled={showOtp} placeholder='Phone' value={phone} onChange={ e=>setPhone(e.target.value) } autoFocus/>
				</div>
				{ !showOtp && <button disabled={sendOTPLoading} className={ sendOTPLoading ?'is-loading' : '' }>{ sendOTPLoading ? 'Sending OTP...' : 'Login / Sign Up' }</button>}
			</form>
			<div id="recaptcha-container"></div>
			{ showOtp && <form onSubmit={(e) => verifyOTP(e)}><p style={{margin: '0.5em 0'}}>Enter OTP</p>
				<OtpInput
					shouldAutoFocus={true}
					value={otp}
					onChange={o => setOTP(o)}
					numInputs={6}
					isInputNum={true}
					inputStyle={{ 
						marginRight: '0.5rem', 
						padding: '1px 2px', 
						fontSize: '1.6rem',
						width: '2.5rem', 
						height: '2.5rem', 
						borderRadius: '.25rem',
						boxSizing: 'border-box'
					}}
					containerStyle={{
						display: 'flex',
						justifyContent: 'center',
						border: '1px solid #eee',
						padding: 20,
						borderRadius: 8
					}}
				/>
				<button 
					disabled={verifyOTPLoading} 
					className={verifyOTPLoading ? 'is-loading' : ''} 
					style={{marginTop: '1rem'}}>
					{verifyOTPLoading ? 'Verifying code...' : 'Verify Code' }
				</button>
			</form>}
		</Fragment>
	);
}