import { useState, useCallback, useRef, useEffect } from 'react';
import { Link, useNavigate } from "react-router-dom"
import { useAuth } from "../auth";
import { getDatabase, ref, onValue } from "firebase/database";

export default function Home() {
  const user = useAuth();
  const navigate = useNavigate();

	const [error, setError] = useState(null)
  const [query, setQuery] = useState("")
	const [consultLoading, setConsultLoading] = useState(false)

  const [answer, setAnswer] = useState("")
  const [verses, setVerses] = useState([])

  const [usedQuota, setUsedQuota] = useState(0)
  const [maxQuota, setMaxQuota] = useState(0)

  const handleChange = async(e) => {
    setQuery(e.target.value)
  }

//   useEffect(() => {
//     const res = verses.map(verse => verse.replace("/\*\*(.*?)\*\*/g", "<b>$1</b>"));
//     console.log(res)
//     setVerses(res)
//   }, [])

  const consultKeyDown = async(e) => {
    if ((e.metaKey || e.ctrlKey)  && (e.key === 'Enter' || e.keyCode === 13)) {
      e.preventDefault();
      consult()
    }
  }

//   useEffect(() => {
//     if(user == undefined) return;
//     console.log("user: ", user)
//     const db = getDatabase();
//     const usageRef = ref(db, "usage/" + user.uid);

//     onValue(usageRef, (snapshot) => {
//         const data = snapshot.val();
//         console.log("data: ", data)
//         // updateStarCount(postElement, data);
//     });
//   }, [user])

  const consult = useCallback(() => {
    setConsultLoading(true)
    console.log("Query: ", query)
    if (!user) {
      return navigate('/login');
    }
    const token = user.accessToken
    const data = {
        query,
        id_token: token
    }
    fetch('https://gitagpt-mwjipadvvq-as.a.run.app/gita', {
        method: 'POST',
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(data)
    }).then(res => res.json())
    .then(responseJson => {
        console.log(responseJson)
        setAnswer(responseJson.answer)
        console.log(responseJson.verses)
        setVerses(responseJson.verses)
        setConsultLoading(false)
        setError(null)
    }).catch(err => {
      setError('Error consulting the Gita. Please try again later.')
      setConsultLoading(false)
    })
  }, [navigate, user, query])



  return (
    <>
        <p>Gita GPT is a GPT-3 powered app that generates answers to your life issues
      from the Bhagavad Gita.</p>

      {error && <p className='error'>{ error }</p>}
        <h2>What troubles you, my child?</h2>
        <textarea autoFocus onChange={handleChange} onKeyDown={consultKeyDown}></textarea>
        <button 
          disabled={consultLoading} 
          className={ consultLoading ?'is-loading' : '' } 
          onClick={() => consult()}
        >
          {consultLoading ? 'Consulting' : 'Consult'} the Gita
        </button>

        {answer && 
        <>
            <h3> Answer</h3>
            <p>{answer}</p>
        </>
        }
        
        {verses.length > 0 && 
            <>
                <h3> Verses</h3>
                {verses.map(verse => 
                    <p><b>{verse[0]}</b> {verse[1]}</p>
                )}
            </>
        }

        {/* <b>Usage: {usedQuota} / {maxQuota}</b> */}
    </>
  );
}

